             <img\r\n                                            style=\"\r\n                                                width: 36px;\r\n                                                height: 36px;\r\n                                                margin: auto;\r\n                                            \"\r\n                                            :src=\"item.icon\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                        <span>{{ item.name }}</span>\r\n                                    </span>\r\n                                </span>\r\n                                <p>â‚¹ {{ item.price }}</p>\r\n                                <p>{{ item.validity }} months</p>\r\n                                <div class=\"sub-details-content\">\r\n                                    <div>100</div>\r\n                                    <div>Standard</div>\r\n                                    <div>1</div>\r\n                                    <div>1</div>\r\n                                    <div>3</div>\r\n                                    <div>50-100kb / Event</div>\r\n                                    <div>50-100kb / Event</div>\r\n                                    <div>\r\n                                        <img\r\n                                            src=\"/assets/login/check.svg\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                    </div>\r\n                                    <div>\r\n                                        <img\r\n                                            src=\"/assets/login/check.svg\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                    </div>\r\n                                    <div>\r\n                                        <img\r\n                                            src=\"/assets/login/check.svg\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                    </div>\r\n                                    <div>\r\n                                        <img\r\n                                            src=\"/assets/login/iconoir_cancel.svg\"\r\n                                            alt=\"\"\r\n                                        />\r\n                                    </div>\r\n                                </div>\r\n                                <button\r\n                                    @click=\"purchase($event, item)\"\r\n                                    :style=\"item.color\"\r\n                                    type=\"button\"\r\n                                >\r\n                                    Choose Plan\r\n                                </button>\r\n                            </div>\r\n                        </li>\r\n                    </ul>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <!-- <div id=\"section-8\">\r\n            <div style=\"margin: auto; overflow-x: auto\">\r\n                <div\r\n                    v-for=\"(item, index) in priceCards\"\r\n                    :key=\"index\"\r\n                    class=\"price-card-container\"\r\n                >\r\n                    <div>\r\n                        <div :style=\"item.color\" class=\"plan\">\r\n                            {{ item.name }}\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"details-cont\">\r\n                        <div>/Per Month</div>\r\n                        <div style=\"text-align: center\">\r\n                            <span>â‚¹</span><span>{{ item.price }}</span>\r\n                        </div>\r\n                        <div>\r\n                            \"Lorem ipsum dolor sit hjbsdc jsnznxk\r\n                            amet,consectetur adipiscing elit, sed do eiusmod\r\n                            tempor incididunt ut labore et dolore magna aliqua.\r\n                            Ut enim ad minim veniam, quis nostru exercitation.\r\n                        </div>\r\n                        <div class=\"pros\">\r\n                            <img src=\"/assets/Vector3.svg\" alt=\"\" />\r\n                            <span\r\n                                >illo inventore veritatis et quasi architecto\r\n                                beatae vitae</span\r\n                            >\r\n                        </div>\r\n                        <div class=\"pros\">\r\n                            <img src=\"/assets/Vector3.svg\" alt=\"\" />\r\n                            <span\r\n                                >quis nostrum exercitationem ullam corporis\r\n                                suscipit laboriosam, nisi ut aliquid ex ea\r\n                                commodi consequatur? Quis autem vel</span\r\n                            >\r\n                        </div>\r\n                        <div class=\"pros\">\r\n                            <img src=\"/assets/Vector4.svg\" alt=\"\" />\r\n                            <span\r\n                                >Duis aute irure dolor in reprehenderit in\r\n                                voluptate velit esse cillum dolore eu\r\n                            </span>\r\n                        </div>\r\n                        <div class=\"pros\">\r\n                            <img src=\"/assets/Vector4.svg\" alt=\"\" />\r\n                            <span\r\n                                >eius modi tempora incidunt ut labore et dolore\r\n                                magnam aliquam quaerat voluptatem.</span\r\n                            >\r\n                        </div>\r\n                        <div style=\"text-align: center; margin: 1em 0\">\r\n                            <button @click=\"purchase($event, item)\" class=\"buy\">\r\n                                Buy Now\r\n                            </button>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div> -->\r\n        <flashMessage :message=\"message\"></flashMessage>\r\n    </div>\r\n</template>\r\n<script>\r\nimport flashMessage from \"../FlashMessage.vue\";\r\n\r\nexport default {\r\n    props: [\"host\", \"plans\"],\r\n    components: {\r\n        flashMessage,\r\n    },\r\n    data() {\r\n        return {\r\n            priceCards: this.plans,\r\n            message: null,\r\n            paid: false,\r\n            cont: null,\r\n        };\r\n    },\r\n    methods: {\r\n        setSplider() {\r\n            let _this = this;\r\n            this.$nextTick(function () {\r\n                _this.cont = new Splide(`#plan-slider`, {\r\n                    gap: 10,\r\n                    rewind: true,\r\n                    autoWidth: true,\r\n                    pagination: false,\r\n                    isNavigation: true,\r\n                    arrows: true,\r\n                    wheel: false,\r\n                    breakpoints: {\r\n                        567: {\r\n                            arrows: false,\r\n                            wheel: true,\r\n                            direction: \"ltr\",\r\n                            releaseWheel: false,\r\n                        },\r\n                    },\r\n                });\r\n                _this.cont.mount();\r\n            });\r\n        },\r\n        purchase(e, item) {\r\n            let _this = this;\r\n            if (this.paid == true) {\r\n                window.location.reload;\r\n                return;\r\n            }\r\n            axios\r\n                .get(route(\"subscribeCreateOrder\", item.id))\r\n                .then((res) => {\r\n                    if (res.data.data?.subscribeable.name == \"Free\") {\r\n                        window.location.href = res.data.redirect;\r\n                        return;\r\n                    }\r\n                    _this.checkout(item.price, res.data.data);\r\n                })\r\n                .catch((err) => {\r\n                    console.log(err);\r\n                });\r\n        },\r\n        checkout(price, data) {\r\n            let _this = this;\r\n            var options = {\r\n                key: \"rzp_live_wLIDlEVBFdFkFS\", // Enter the Key ID generated from the Dashboard\r\n                amount: price * 100, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise\r\n                currency: \"INR\",\r\n                name: \"HappyWed\",\r\n                description: \"Test Transaction\",\r\n                image: \"http://wedding.test/assets/login/logo.png\",\r\n                order_id: data.order_id, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1\r\n                handler: function (response) {\r\n                    let formData = new FormData();\r\n                    this.paid = true;\r\n                    formData.append(\"id\", data.id);\r\n                    formData.append(\"data\", JSON.stringify(response));\r\n                    axios({\r\n                        method: \"post\",\r\n                        url: route(\"subscribeVerifyPayment\"),\r\n                        data: formData,\r\n                    })\r\n                        .then((res) => {\r\n                            window.location.reload();\r\n                        })\r\n                        .catch((err) => {\r\n                            console.log(err);\r\n                        });\r\n                },\r\n                prefill: {\r\n                    name: _this.host.name,\r\n                    email: _this.host.email,\r\n                    contact: _this.host.mobile,\r\n                },\r\n                notes: {\r\n                    address: \"Razor